import React, {useEffect , useState, useRef} from 'react';

import {Button, Panel, PanelHeader, ScreenSpinner, Snackbar, Avatar} from '@vkontakte/vkui/';
import {Icon16ErrorCircleOutline} from '@vkontakte/icons'

import fire_fon_img from '../img/fire_fon_img.jpg'
import tropical_rain_fon_img from '../img/tropical_rain_fon_img.jpg'
import crystal_cave_fon_img from '../img/crystal_cave_fon_img.jpg'
import garden_of_eden_fon_img from '../img/garden_of_eden_fon_img.jpg'
import rest_on_the_shore_fon_img from '../img/rest_on_the_shore_fon_img.jpg'
import light_rain_fon_img from '../img/light_rain_fon_img.jpg' 
import storm_fon_img from '../img/storm_fon_img.jpg' 
import forward_to_the_stars_fon_img from '../img/forward_to_the_stars_fon_img.jpg' 

import './Song.css'

const Song = ({ id, setPlaer, image, setPopout, timer, restartx, play }) => {
	const [fon, setFon] = useState(fire_fon_img)
	const [timex, setTimex] = useState(timer.current)
	const [timerBody, setTimerBody] = useState(null)
	const int = useRef(null)
	const [restart, setRestart] = useState(false)
	const [snackbar, setSnackbar] = useState(false)


	const setTimerx = () => {
		const text = document.getElementById('timer-input').value
		const date = text.split(':')
		timer.current = +date[1] * 60
		timer.current += +date[0] * 3600

		if (isNaN(timer.current)) {
			timer.current = -1
			setSnackbar(
				<Snackbar
            		onClose={() => setSnackbar(null)}
            		before={<Avatar size={24} style={{ background: 'var(--destructive)' }}>
            		<Icon16ErrorCircleOutline fill="#fff" width={14} height={14} />
            		</Avatar>}>
            		Введите корректное время!
          		</Snackbar>
			)
		}

		setTimex(timer.current)
		restartx()
		setRestart(!restart)
		
	}

	useEffect(() => {
		if (timer.current < 0) {
			setTimex(0)
			setTimerBody(
		<div className="timer-input-body">
			<div className="timer-input-header">АВТОМАТИЧЕСКОЕ ОТКЛЮЧЕНИЕ:</div>
			<div className='timer-input-timer'>
			<input 
			id='timer-input'
			className='timer-input'
			type="time" />
			<Button className='timer-input-button' onClick={setTimerx}>Установить</Button>
			</div>
			
		</div>)
		} else {
			setTimex(timer.current)
			int.current = setInterval(() => {
				
				setTimex(timex - 1)
				console.log(timex)
				setTimerBody(
					<div>
						<div className="timer-exit-header"
						style={{textAlign: 'center'}}>ОТКЛЮЧЕНИЕ ЧЕРЕЗ:</div>
						<div className="timer-exit">
						<div className="timer-exit-header"
						style={{textAlign: 'center'}}>
							{`${(Math.floor(timex) / 3600 < 10 ? '0' + (Math.floor(timex / 3600)).toFixed(0) : 
							(Math.floor(timex / 3600)).toFixed(0))} час.
							${Math.floor(timex % 3600 / 60) < 10 ? '0' + 
							(Math.floor(timex  % 3600 / 60)).toFixed(0) : 
							(Math.floor(timex  % 3600 / 60)).toFixed(0)} мин.
							${timex % 3600 % 60  < 10 ? '0' + (timex  % 3600 % 60 ).toFixed(0) : (timex  % 3600 % 60).toFixed(0)} сек.`}
						</div>
						<Button 
							className='timer-input-button' 
							style={{height: 14}}
							onClick={() => {
								timer.current = -1
								clearInterval(int.current)
								setRestart(!restart)
							}}>Отмена</Button>
						</div>
					</div>
					
					
				)
			}, 1000)
	
			return () => {
				clearInterval(int.current)
			}
		}
		
	}, [timer.current ,restart])

	useEffect(() => {
			switch (image) {
				case 1 :
					setFon(fire_fon_img)
					break
				case 2 :
					setFon(tropical_rain_fon_img)
					break
				case 3 :
					setFon(crystal_cave_fon_img)
					break
				case 4 :
					setFon(garden_of_eden_fon_img)
					break
				case 5 :
					setFon(rest_on_the_shore_fon_img)
					break
				case 6 :
					setFon(light_rain_fon_img)
					break
				case 7 :
					setFon(storm_fon_img)
					break
				case 8 :
					setFon(forward_to_the_stars_fon_img)
					break
			}
			setPopout(<ScreenSpinner size='large' />)

			setTimeout(() => {
				setPopout(null)
			}, 1000)
	}, [])

	return (
		<Panel id={id}>
		<PanelHeader>Плеер</PanelHeader>
		<div className='song-menu' style={{backgroundImage: `url(${fon})`}}>
			<div className='equalizer-div'>
			<button className="equalizer">
   				<span className="eq1"></span>
   				<span className="eq2"></span>
   				<span className="eq3"></span>
				<span className="eq4"></span>
				<span className="eq5"></span>
			</button>
			</div>
		</div>
		{timerBody}
		<div className='active-button' onClick={() => {
			setPlaer()
		}}><div>{!play ? "ВКЛ" : "ВЫКЛ"}</div></div>
		{snackbar}
		</Panel>
	)
} 


export default Song;