import React, { useState, useEffect, useRef } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import { AdaptivityProvider, AppRoot, Root, Epic, Tabbar, TabbarItem } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import {
	Icon20PodcastCircleFillRed,
	Icon20RadiowavesLeftAndRightCircleFillRed,
	Icon28GearCircleFillGray
  } from '@vkontakte/icons/'

  import './App.css'


import Home from './panels/Home';
import Song from './panels/Song';
import Settings from './panels/Settings';

const App = () => {
	const [activeMenu, setActiveMenu] = useState('home-menu')
	const [activeView, setActiveView] = useState('home')
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);
	const timer = useRef(-1)
	const [timerInterval, setTimerInterval] = useState(null)
	const int = useRef(null)
	
	const [audio, setAudio] = useState(null)
	const [play, setPlay] = useState(false)
	const [thisAudio, setThisAudio] = useState(new Audio())

	const [restartTimer, setRestartTimer] = useState(false)

	const [image, setImage] = useState(1)


	const restartx = () => {
		setRestartTimer(!restartTimer)
	}

	const setPlaer = () => {
		setPlay(!play)
	}

	const setTimer = (x) => {
		timer.current = x
	}

	useEffect(() => {
		console.log(timerInterval)
		if (timerInterval) return
		if(timer.current > 0) {
			const interval = setInterval(() => {
				
				if (timer.current === 0) {
					thisAudio.pause()
				}
				timer.current = timer.current - 1
				if(timer.current < 0) {
					clearInterval(int.current)
					setTimerInterval(null)
				} 
			}, 1000)
			int.current = interval
			setTimerInterval(interval)
		}
	}, [restartTimer])


	useEffect(() => {
		if (!audio) return
		if (play) {
			setPlay(!play)
		}
		thisAudio.src = audio
		thisAudio.loop = true;
	}, [audio] )

	useEffect(() => {
		if (!audio) return
		if (play) {
			thisAudio.play()
		} else {
			thisAudio.pause()
		}
		
	}, [play])

	useEffect(() => {
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const goView = (e) => {
		setActiveView(e.currentTarget.dataset.story)
	  }
	
	  const goMenu = (e) => {
		setActiveMenu(e)
	  }
	
	  const goGame = (e) => {
		setActiveGame(e)
	  }

	return (
		<AdaptivityProvider>
<AppRoot>
<Root activeView={activeMenu}>
	<View id="home-menu">
		<Epic
          	activeStory={activeView}
          	tabbar={
            	<Tabbar>
            	  <TabbarItem
            	    onClick={goView}
            	    selected={activeView === 'home'}
            	    data-story="home"
            	    text="Звуки"
            	  >
            		<Icon20PodcastCircleFillRed width={28} height={28} />
					</TabbarItem>
					<TabbarItem
            	    onClick={goView}
            	    selected={activeView === 'song'}
            	    data-story="song"
            	    text="Слушать"
            	  >
            		<Icon20RadiowavesLeftAndRightCircleFillRed width={28} height={28} />
					</TabbarItem>
					<TabbarItem
            	    onClick={goView}
            	    selected={activeView === 'settings'}
            	    data-story="settings"
            	    text="Настройки"
            	  >
            		<Icon28GearCircleFillGray />
					</TabbarItem>
            	</Tabbar>
          	}
        		>
        <View id="home" activePanel="home" popout={popout}>
			<Home 
			id="home"
			goView={goView}
			setAudio={setAudio}
			setImage={setImage}
			/>
        </View>
		<View id="song" activePanel="song" popout={popout}>
			<Song 
			id="song"
			setPlaer={setPlaer}
			image={image}
			setPopout={setPopout}
			timer={timer}
			setTimer={setTimer}
			restartx={restartx}
			play={play}/>
        </View>
		<View id="settings" activePanel="settings" popout={popout}>
			<Settings id="settings"fetchedUser={fetchedUser}/>
        </View>
        </Epic>
	</View>
</Root>
</AppRoot>
</AdaptivityProvider>
	);
}

export default App;

